// Links

a {
  
  &, &:focus, &:active, &:hover {
    text-decoration: none;
  }
  
  @include link($link-color, $link-hover-color);
  
  &[href*='callto'],
  &[href*='mailto'] {
    white-space: nowrap;
  }
}

.link-hover{
  color: $link-hover-color;
}

.link-press{
  color: $link-press-color;
}
