// Import default color scheme
@import "color-scheme/color-default";

// Import variables
@import "variables-custom";
// Import mixins
@import "mixins";
// Import reset styles
@import "reset";

// Components & modules & plugins & helpers
//

@mixin context-dark {
  &,
  #{headings()} {
    color: $white;
  }

  // Buttons
  .button-primary {
    &:hover {
      color: $gray-darker;
      background: $gray-lighter;
      border-color: $gray-lighter;
    }
  }
}

.context-image-dark {
  @include context-dark;
}

%context-dark {
  @include context-dark;

  // Logo
  .brand {
    .brand-logo-dark { display: none; }
    .brand-logo-light { display: block; }
  }
}
@media print {
  @page {
    margin: .5in
  }
  h6 {
    //page-break-before: always;
  }
  .shell { 
    width: 100% !important;
    max-width: 100% !important;
    padding: 0 !important;
  }
  
  header,
  footer {
    padding-left: 15px !important;
    padding-right: 15px !important;
    
    h6 {
      text-align: right !important;
    }
  }
}
@import "includes";
@import "components/_layout-panel";

