/*
*
* Typography
* ==================================================
*/

// Body

body {
  font-family: $font-family-base;
  font-size: $font-size-base;
  line-height: 1.35;
  font-weight: $font-weight-base;
  color: $text-color;
  background-color: $body-bg;
  -webkit-text-size-adjust: none;
  -webkit-font-smoothing: subpixel-antialiased;
  @media (min-width: $screen-xl-min) {
    line-height: $line-height-base;
  }
}

p {
  * {
    display: inline;
  }

  a {
    color: $primary;
  }

  [data-toggle='tooltip'] {
    padding-left: .25em;
    padding-right: .25em;
    color: $primary;
  }
}

a {
  color: $link-color;
  text-decoration: none;
  transition: .25s;

  &:hover,
  &:focus {
    color: $link-hover-color;
    text-decoration: $link-hover-decoration;
    outline: none;
  }

  &[href*='callto'],
  &[href*='mailto'] {
    white-space: nowrap;
  }
}

.link-hover {
  color: $link-hover-color;
}

.link-press {
  color: $link-press-color;
}

#{headings()} {
  margin-top: 0;
  margin-bottom: 0;
  font-family: $headings-font-family;
  font-weight: $headings-font-weight;
  color: $headings-color;

  a {
    color: inherit;

    &:hover {
      color: $primary;
    }
  }
}

h1,
.heading-1 {
  font-size: 32px;
  line-height: 1.5;

  @media (min-width: $screen-xl-min) {
    line-height: $font-lh-h1;
    font-size: $font-size-h1;
  }
}

h2,
.heading-2 {
  font-size: 28px;
  line-height: 1.5;

  @media (min-width: $screen-xl-min) {
    line-height: $font-lh-h2;
    font-size: $font-size-h2;
  }
}

h3,
.heading-3 {
  font-size: 24px;
  line-height: 1.5;

  @media (min-width: $screen-xl-min) {
    line-height: $font-lh-h3;
    font-size: $font-size-h3;
  }
}

h4,
.heading-4 {
  font-size: 22px;
  line-height: 1.5;

  @media (min-width: $screen-xl-min) {
    line-height: $font-lh-h4;
    font-size: $font-size-h4;
  }
}

h5,
.heading-5 {
  font-size: 20px;
  line-height: 1.5;

  @media (min-width: $screen-xl-min) {
    line-height: $font-lh-h5;
    font-size: $font-size-h5;
  }
}

h6,
.heading-6 {
  font-size: 18px;
  line-height: 1.5;

  @media (min-width: $screen-xl-min) {
    line-height: $font-lh-h6;
    font-size: $font-size-h6;
  }
}

// Big text
.big {
  font-size: $font-size-large;
}

// Small text
small,
.small {
  display: block;
  font-size: $font-size-small;
}

mark,
.mark {
  padding: 3px 5px;
  color: $white-invariable;
  background: $primary;
}

code {
  padding: 3px 5px;
  border-radius: $border-radius-small;
  color: $gray-dark;
  background: $gray-lighter;
}

pre {
  tab-size: 2;
}

.text-block {
  > * {
    margin-left: .125em;
    margin-right: .125em;
  }
}

// Logo
.brand {
  display: inline-block;
  .brand-logo-light {
    display: none;
  }
  .brand-logo-dark {
    display: block;
  }
}

.brand-logo {
  display: flex;
  align-items: center;
  font-family: $headings-font-family;
  letter-spacing: .04em;
  color: $white;
  > * + * {
    margin-left: 5px;
  }
  
  &:hover,
  &:active,
  &:focus {
    color: $white;
  }
}

// section styles
.main-section {
  position: relative;
  min-height: calc(100vh - 80px);
}

.main-section-inner {
  border: 1px solid $gray-lighter;
  box-shadow: 0 0 25px rgba($gray-dark, .05);
  padding: 30px 15px;

  @media (min-width: $screen-sm-min) {
    padding: 45px 40px;
  }
}

// data-type 
* + [data-type='anchor'] {
  padding-top: 40px;
  padding-bottom: 40px;
  border-bottom: 1px solid $gray-lighter;
}

// boxes
.box {
  * + & {
    margin-top: 45px;
  }
}

* + .box-title {
  margin-top: 15px;
}

* + .box-content {
  margin-top: 15px;
}

// gallery item

.gallery-item {
  position: relative;
  display: inline-block;
  width: auto;
  
  img {
    width: 100%;
  }

  &:before {
    position: absolute;
    top: 10px;
    right: 10px;
    display: flex;
    align-items: flex-start;
    justify-content: flex-end;
    content: "\f065"; 
    font: 400 20px/1 'FontAwesome';
    opacity: 0;
    color: $white;
    transform: scale(0);
    transition: 350ms ease-in-out;
    z-index: 1;
  }
  &:after {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    content: '';
    opacity: 0;
    background: rgba($gray-darker, .3);
    transition: 350ms ease-in-out; 
  }

  &:hover {
    &:before,
    &:after {
      transform: scale(1);
      opacity: 1;
    }
  }
}

.d-flex {
  display: flex;
  
  > * {
    flex-grow: 1;
  }
  
  img {
    
  }
}
