//== Colors
//


$gray-base:               #000;
$white:                   #fff;

$gray-darker:             #1F2532;
$gray-dark:               #333;
$gray:                    #666;
$gray-light:              #b7b7b7;
$gray-lighter:            #f2f3f8;

$primary:                 #109DF7; 


//== Scaffolding
//

//Body
$body-bg:               $white;

// Text
$text-color:            $gray;

// link
$link-color:            $primary;
$link-hover-color:      darken($link-color, 15%);
$link-press-color:      darken($link-color, 15%);
$link-hover-decoration: none;

// Heading color
$headings-color:        $gray-dark; //