/*
*
* Page Loaders
* --------------------------------------------------
*/

// Base styles
// -------------------------

.page-loader {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  z-index: 9999999;
  @include display-flex;
  @include align-items(center);
  @include justify-content(center);
  flex-direction: column;
  padding: 20px;
  background: $white;
  transition: .3s all ease;

  > * + * {
    margin-top: 10px;
  }

  .page-loader-body {
    text-align: center;
  }
  
  h5 {
    font-family: 'Arial', sans-serif;
  }
}

// Custom Styles
// -------------------------
[class*='dot'] {
  display: inline-block;
  width: 3px;
  height: 3px;
  background-color: $gray-darker;

  margin-left: 2px;
  margin-right: 2px;

  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  animation-fill-mode: forwards;
  opacity: 0; 
}
.dot-1 {
  animation-name: dotAnim;
}
.dot-2 {
  animation-name: dotAnim1;
}
.dot-3 {
  animation-name: dotAnim2;
}

@keyframes dotAnim {
  0% {
    opacity: 1;
  }
}
@keyframes dotAnim1 {
  33.3% {
    opacity: 1;
  }
}
@keyframes dotAnim2 {
  66.6% {
    opacity: 1;
  }
}
